import React, { FC } from "react";

import { CategoryCoinType, CategoryTypes } from "config/types";

import styles from "./CategoryCoin.module.scss";

const CategoryCoin: FC<CategoryCoinType> = ({
  clickMenuOption,
  categories,
}) => {
  const [activeId, setActiveId] = React.useState(1);

  const changeMenu = (category: CategoryTypes) => {
    setActiveId(category.id);
    clickMenuOption(category);
  };

  return (
    <div className={styles.category}>
      {categories.map((category) => (
        <h6
          key={category.id}
          onClick={() => changeMenu(category)}
          className={category.id === activeId ? "active" : ""}
        >
          {category.content}
        </h6>
      ))}
    </div>
  );
};

export default CategoryCoin;
