import React, { FC, useMemo } from "react";

import Line from "components/Line";
import { CoinProps, TableRow } from "config/types";
import { getFormattedTableValue } from "utils/getFormattedPrice";

import styles from "./CoinTable.module.scss";

const CoinTable: FC<CoinProps> = ({ coin }) => {
  let marketData = coin?.marketData;
  const tableValue: TableRow[] = [
    {
      key: 0,
      title: "Market Cap",
      value: `$${getFormattedTableValue(marketData?.marketCap?.usd)}`,
    },
    {
      key: 1,
      title: "Fully Diluted Valuation",
      value: `$${getFormattedTableValue(
        marketData?.fullyDilutedValuation?.usd
      )}`,
    },
    {
      key: 2,
      title: "Circulating Supply",
      value: getFormattedTableValue(marketData?.circulatingSupply),
    },
    {
      key: 3,
      title: "Total Supply",
      value: getFormattedTableValue(marketData?.totalSupply),
    },
    {
      key: 4,
      title: "Max Supply",
      value: getFormattedTableValue(marketData?.maxSupply),
    },
  ];

  const generateTable = useMemo(() => {
    return tableValue.map((value) => (
      <div key={value.key}>
        <div className={styles.table__head}>
          <p className={styles.table__head_title}>{value.title}</p>
          <p className={styles.table__head_value}>{value.value || 0}</p>
        </div>
        <Line />
      </div>
    ));
  }, []);
  return <div className={styles.table}>{generateTable}</div>;
};

export default CoinTable;
