import React from "react";

import Loader from "components/Loader";
import { LoaderSize } from "components/Loader/Loader";
import { Meta } from "config/meta";
import { observer } from "mobx-react-lite";
import { Link, useParams } from "react-router-dom";
import CoinsStore from "store/CoinStore/CoinStore";
import { useLocalStore } from "store/useLocalStore/useLocalStore";

import styles from "./CoinPage.module.scss";
import Coin from "./components/Coin/Coin";

const CoinPage = () => {
  const coinsStore = useLocalStore(() => new CoinsStore());
  const { coinID } = useParams();

  React.useEffect(() => {
    coinsStore.getCoin(coinID);
  }, [coinsStore, coinID]);

  return (
    <div className={styles.coin}>
      {coinsStore.meta === Meta.loading ? (
        <Loader size={LoaderSize.l} loading={true} />
      ) : coinsStore.meta === Meta.error ? (
        <div className={styles.coin__error}>
          ...   ! <Link to="/"> </Link>
        </div>
      ) : (
        <Coin coin={coinsStore.coin} />
      )}
    </div>
  );
};

export default observer(CoinPage);
