import React, { FC } from "react";

import { useStocksStoreContext } from "app/pages/MarketPage/MarketPage";
import Loader, { LoaderSize } from "components/Loader/Loader";
import { observer } from "mobx-react-lite";
import InfiniteScroll from "react-infinite-scroll-component";
import { StockItemsModels } from "store/models";

import CoinCard from "./CoinCard/CoinCard";
import styles from "./CoinCards.module.scss";

type CoinCardsList = {
  stocks: StockItemsModels[] | undefined;
};

const CoinCards: FC<CoinCardsList> = ({ stocks }) => {
  const stockStore = useStocksStoreContext();
  return (
    <div className={styles.cards}>
      <InfiniteScroll
        dataLength={stockStore?.list.length || 0}
        next={() => stockStore?.fetchMoreData()}
        hasMore={true}
        loader={<Loader loading={true} size={LoaderSize.s} />}
        endMessage={
          <p style={{ textAlign: "center" }}>
            <b>Yay! You have seen it all</b>
          </p>
        }
      >
        {stocks
          ? stocks.map((stock) => (
              <CoinCard
                stock={stock}
                key={stock.id}
                currency={stockStore?.currency}
              />
            ))
          : stockStore?.list.map((stock) => (
              <CoinCard
                stock={stock}
                key={stock.id}
                currency={stockStore?.currency}
              />
            ))}
      </InfiniteScroll>
    </div>
  );
};

export default observer(CoinCards);
