import React, { useCallback, useState } from "react";

import { useStocksStoreContext } from "app/pages/MarketPage/MarketPage";
import { CategoriesTypes, MultiDropdownProps } from "config/types";
import { observer } from "mobx-react-lite";
import { useLocalStore } from "store/useLocalStore/useLocalStore";

import styles from "./MultiDropdown.module.scss";

const MultiDropdown: React.FC<MultiDropdownProps> = ({
  options,
  clickOption,
}) => {
  const [isOpen, setIsOpen] = useState<boolean>(false);
  const stockStore = useStocksStoreContext();

  React.useEffect(() => {
    stockStore?.getCategories();
  }, [stockStore]);

  const chooseOption = useCallback((option: CategoriesTypes) => {
    return (e: React.MouseEvent) => {
      setIsOpen((prevValue) => !prevValue);
      clickOption(option);
    };
  }, []);

  return (
    <div className={styles.multiDropdown}>
      <div
        className={styles.multiDropdown__button}
        onClick={() => {
          setIsOpen((prevValue) => !prevValue);
        }}
      >
        <div className={styles.multiDropdown__button_value}>
          {stockStore?.category.map((el: CategoriesTypes) => el.name).join() ||
            "Choose category"}
        </div>
        <div className={styles.multiDropdown__button_arrow}></div>
      </div>
      {isOpen && (
        <div className={styles.options} id="options">
          {options?.map((item) => (
            <label key={item.category_id} onClick={chooseOption(item)}>
              <input
                type="checkbox"
                name="checkbox"
                className={styles.options__item}
              />
              {item.name}
            </label>
          ))}
        </div>
      )}
    </div>
  );
};
export default observer(MultiDropdown);
