import React from "react";

import classNames from "classnames";
import "./Loader.scss";

export enum LoaderSize {
  s = "s",
  m = "m",
  l = "l",
}

export type LoaderProps = {
  loading?: boolean;
  size?: LoaderSize;
};

const Loader: React.FC<LoaderProps> = ({
  loading = true,
  size = LoaderSize.m,
}) => {
  if (size === undefined) size = LoaderSize.m;
  let cN = classNames("loader", `loader-size-${size}`);

  return (
    <div className={`loader_background-${size}`}>
      {loading && <div className={cN}></div>}
    </div>
  );
};

export default Loader;
