import { COUNT_COINS, currencyIcon } from "config/const";
import { Menu } from "config/menu";
import { Meta } from "config/meta";
import { CategoriesTypes, IStocksStore } from "config/types";
import {
  action,
  computed,
  makeObservable,
  observable,
  runInAction,
} from "mobx";
import ApiStore from "store/ApiStore/ApiStore";
import {
  CollectionModel,
  getInitialCollectionModel,
  linearizeCollection,
  normalizeCollection,
} from "store/models/shared/collection";

import {
  normalizeStockItems,
  StockItemsApi,
  StockItemsModels,
} from "../models";
import { ILocalStore } from "../useLocalStore/useLocalStore";

const BASE_URL = "https://api.coingecko.com/api/v3/coins/";

type PrivateFields =
  | "_list"
  | "_meta"
  | "_menu"
  | "_value"
  | "_hasMore"
  | "_categories"
  | "_category"
  | "_page"
  | "_currency";

export default class StocksStore implements IStocksStore, ILocalStore {
  private readonly _apiStore = new ApiStore(BASE_URL);
  private _list: CollectionModel<string, StockItemsModels> =
    getInitialCollectionModel();
  private _meta: Meta = Meta.initial;
  private _menu: Menu = Menu.All;
  private _value: string = "";
  private _hasMore: boolean = true;
  private _currency: string = "$";
  private _category: CategoriesTypes[] = [];
  private _categories: CategoriesTypes[] | null = null;
  private _page: number = 1;

  constructor() {
    makeObservable<StocksStore, PrivateFields>(this, {
      _list: observable.ref,
      _meta: observable,
      _menu: observable,
      _value: observable,
      _currency: observable,
      _hasMore: observable,
      _categories: observable,
      _category: observable,
      _page: observable,

      list: computed,
      meta: computed,
      menu: computed,
      value: computed,
      currency: computed,
      categories: computed,
      category: computed,
      hasMore: computed,
      page: computed,

      getStocksList: action.bound,
      setValue: action.bound,
      setCurrency: action.bound,
      fetchMoreData: action.bound,
      getCategories: action.bound,
      getCategory: action.bound,
      setMenu: action.bound,
      getList: action.bound,
      getGainerList: action.bound,
      getLoserList: action.bound,
    });
  }

  get list(): StockItemsModels[] {
    return linearizeCollection(this._list);
  }

  get meta(): Meta {
    return this._meta;
  }

  get menu(): Menu {
    return this._menu;
  }

  get value(): string {
    return this._value;
  }

  get currency(): string {
    return this._currency;
  }

  get hasMore(): boolean {
    return this._hasMore;
  }

  get page(): number {
    return this._page;
  }

  get categories(): CategoriesTypes[] | null {
    return this._categories;
  }

  get category(): CategoriesTypes[] {
    return this._category;
  }

  getList() {
    return this.list;
  }

  getGainerList() {
    if (this.list.length > 0) {
      return this.list.filter(
        (item) =>
          item.priceChangePercentage24h >= 0 &&
          item.priceChangePercentage24h !== undefined
      );
    }
  }

  getLoserList() {
    if (this.list.length > 0) {
      return this.list.filter(
        (item) =>
          item.priceChangePercentage24h < 0 &&
          item.priceChangePercentage24h !== undefined
      );
    }
  }

  getCategory(cat: CategoriesTypes) {
    let haveCategory: boolean = this._category.some(
      (item) => cat.category_id === item.category_id
    );
    if (!haveCategory) {
      this._category = this._category.concat([cat]);
    }
    this.getStocksList();
    if (this._menu === Menu.Gainer) {
      this.getGainerList();
    }
    if (this._menu === Menu.Loser) {
      this.getLoserList();
    }
  }

  setValue(value: string) {
    this._value = value;
  }

  setMenu(menu: Menu) {
    this._menu = menu;
  }

  setCurrency(query: string) {
    this._currency = currencyIcon[query];
  }

  async fetchMoreData(): Promise<void> {
    let url = `markets?vs_currency=usd&per_page=${COUNT_COINS}&page=${this._page}`;
    if (this.category.length > 0) {
      url = `markets?vs_currency=usd&per_page=${COUNT_COINS}&page=${
        this._page
      }&category=${this.category
        .map((el: CategoriesTypes) => el.category_id)
        .join()}`;
    }

    const response = await this._apiStore.request<StockItemsApi[] | null>({
      endpoint: url,
    });

    runInAction(() => {
      if (response.data.length < COUNT_COINS) {
        this._hasMore = false;
      }
      let obj = {};
      const list = response.data.map(normalizeStockItems);
      list.forEach((item: StockItemsModels) => {
        obj = {
          ...obj,
          [item.id]: item,
        };
      });
      this._list = {
        entities: { ...obj, ...this._list.entities },
        order: [
          ...this._list.order,
          ...response.data.map((item: { id: string }) => item.id),
        ],
      };

      this._page = Math.floor(this._list.order.length / COUNT_COINS);
      let fetch = this.fetchMoreData;
      fetch();
      obj = {};
    });
  }

  async getStocksList(): Promise<void> {
    this._meta = Meta.loading;
    this._list = getInitialCollectionModel();
    this._hasMore = true;

    let url: string;
    if (this.category.length > 0) {
      url = `markets?vs_currency=usd&per_page=${COUNT_COINS}&category=${this.category
        .map((el: CategoriesTypes) => el.category_id)
        .join()}`;
    } else {
      url = `markets?vs_currency=usd&per_page=${COUNT_COINS}`;
    }

    const response = await this._apiStore.request<StockItemsApi[] | null>({
      endpoint: url,
    });

    runInAction(() => {
      if (!response.success) {
        this._meta = Meta.error;
      }
      try {
        const list = response.data.map(normalizeStockItems);
        this._meta = Meta.success;
        this._list = normalizeCollection(list, (listItem) => listItem.id);
        if (response.data.length < COUNT_COINS) {
          this._hasMore = false;
        }
        if (this.menu === Menu.Gainer) {
          this.getGainerList();
        }
        return;
      } catch {
        this._meta = Meta.error;
        this._list = getInitialCollectionModel();
      }
    });
  }

  async getCategories(): Promise<void> {
    const response = await this._apiStore.request<CategoriesTypes[] | null>({
      endpoint: `categories/list`,
    });
    runInAction(() => {
      try {
        this._categories = response.data;
        return;
      } catch {
        throw Error("error");
      }
    });
  }

  async setSearchList(currency: string): Promise<void> {
    this._meta = Meta.loading;
    let url: string;
    if (this.category.length > 0) {
      url = `markets?vs_currency=${currency}&category=${this.category
        .map((el: CategoriesTypes) => el.category_id)
        .join()}`;
    } else {
      url = `markets?vs_currency=${currency}`;
    }

    const response = await this._apiStore.request<StockItemsApi[] | null>({
      endpoint: url,
    });

    runInAction(() => {
      if (!response.success) {
        this._meta = Meta.error;
      }
      try {
        const list = response.data.map(normalizeStockItems);
        this._meta = Meta.success;
        this._list = normalizeCollection(list, (listItem) => listItem.id);
        return;
      } catch {
        this._meta = Meta.error;
        this._list = getInitialCollectionModel();
      }
    });
  }

  destroy(): void {}
}
