import React, { createContext, FC, useContext, useState } from "react";

import Loader from "components/Loader";
import { LoaderSize } from "components/Loader/Loader";
import MultiDropdown from "components/Multidropdown";
import { Menu } from "config/menu";
import { Meta } from "config/meta";
import { CategoriesTypes, CategoryTypes } from "config/types";
import { observer } from "mobx-react-lite";
import { useSearchParams } from "react-router-dom";
import { StockItemsModels } from "store/models";
import { useQueryParamsStoreInit } from "store/RootStore/hooks/useQueryParamsStoreInit";
import StocksStore from "store/StocksStore/StocksStore";
import { useLocalStore } from "store/useLocalStore/useLocalStore";

import CategoryCoin from "./components/CategoryCoin";
import CoinCards from "./components/CoinCards";
import InputSearch from "./components/InputSearch";
import styles from "./MarketPage.module.scss";
import Line from "../../components/Line";

export const StocksStoreContext = createContext<StocksStore | null>(null);
export const useStocksStoreContext = () => useContext(StocksStoreContext);

const MarketPage: FC = () => {
  useQueryParamsStoreInit();
  const [stocks, setStocks] = useState<StockItemsModels[] | undefined>(
    undefined
  );
  const stocksStore = useLocalStore(() => new StocksStore());
  const [searchParams, setSarchParams] = useSearchParams();
  const query = searchParams.get("vs_currency") || "";

  const categories: CategoryTypes[] = [
    { id: 1, content: Menu.All, list: stocksStore.getList() },
    {
      id: 2,
      content: Menu.Gainer,
      list: stocksStore.getGainerList(),
    },
    {
      id: 3,
      content: Menu.Loser,
      list: stocksStore.getLoserList(),
    },
  ];

  const clickMenuOption = (category: CategoryTypes) => {
    stocksStore?.setMenu(category.content);
    setStocks(category.list);
  };

  React.useEffect(() => {
    stocksStore.getStocksList();
  }, [stocksStore]);

  const handleChange = (value: string) => {
    stocksStore.setValue(value);
  };

  const handleSubmit = () => {
    const query = stocksStore.value.toLowerCase();
    stocksStore.setSearchList(query);
    stocksStore.setCurrency(query.toLowerCase());
    setSarchParams({
      ...searchParams,
      vs_currency: query,
    });
  };

  const clickOption = (option: CategoriesTypes) => {
    stocksStore?.getCategory(option);
    setSarchParams({
      ...searchParams,
      category: stocksStore.category
        .map((el: CategoriesTypes) => el.category_id)
        .join(),
    });
  };

  return (
    <StocksStoreContext.Provider value={stocksStore}>
      <div className={styles.market}>
        <InputSearch
          value={stocksStore.value}
          handleChange={handleChange}
          handleSubmit={handleSubmit}
        />
        <div className={styles.market__multidropdown}>
          <h4>Coins</h4>
          <MultiDropdown
            options={stocksStore.categories}
            value={stocksStore.categories}
            clickOption={clickOption}
          />
        </div>
        <CategoryCoin
          clickMenuOption={clickMenuOption}
          categories={categories}
        />
        <Line />
        {stocksStore.meta === Meta.loading ? (
          <Loader loading={true} size={LoaderSize.m} />
        ) : (
          <CoinCards stocks={stocks} />
        )}
      </div>
    </StocksStoreContext.Provider>
  );
};

export default observer(MarketPage);
