import { Meta } from "config/meta";
import { ICoinsStore } from "config/types";
import {
  action,
  computed,
  makeObservable,
  observable,
  runInAction,
} from "mobx";
import ApiStore from "store/ApiStore/ApiStore";
import { CoinApi, CoinModels, normalizeCoin } from "store/models/Coin";

import { ILocalStore } from "../useLocalStore/useLocalStore";

const BASE_URL = "https://api.coingecko.com/api/v3/coins/";

type PrivateFields = "_meta" | "_coin";

export default class CoinsStore implements ICoinsStore, ILocalStore {
  private readonly _apiStore = new ApiStore(BASE_URL);
  private _coin: CoinModels | null = null;
  private _meta: Meta = Meta.initial;

  constructor() {
    makeObservable<CoinsStore, PrivateFields>(this, {
      _coin: observable.ref,
      _meta: observable,

      meta: computed,
      coin: computed,

      getCoin: action,
    });
  }

  get meta(): Meta {
    return this._meta;
  }

  get coin(): CoinModels | null {
    return this._coin;
  }

  async getCoin(id: string | undefined): Promise<void> {
    this._meta = Meta.loading;
    this._coin = null;
    if (id === undefined) {
      this._meta = Meta.error;
      this._coin = null;
      return;
    }

    const response = await this._apiStore.request<CoinApi[] | null>({
      endpoint: `/${id}`,
    });

    runInAction(() => {
      if (!response.success) {
        this._meta = Meta.error;
        this._coin = null;
        return;
      }
      try {
        this._meta = Meta.success;
        this._coin = normalizeCoin(response.data);
        return;
      } catch {
        this._meta = Meta.error;
        this._coin = null;
      }
    });
  }

  destroy(): void {}
}
