export const getFormattedPrice = (price?: number): string | number => {
  if (price === undefined) {
    return 0;
  }
  if (price > 1000000) {
    price = price / 1000000;
    return `${price
      .toFixed(3)
      .toString()
      .replace(/\B(?=(\d{3})+(?!\d))/g, ",")}M`;
  }
  return price
    .toFixed(2)
    .toString()
    .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
};

export const getFormattedTableValue = (
  price?: number
): string | number | undefined => {
  if (price === undefined) {
    return 0;
  }
  if (typeof price === "number") {
    return price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  }
};
